import React from 'react'
import trash from '../assets/img/trash.svg'
import mark from '../assets/img/mark.png'

const ToDoList = ({ removeTask, toggleTodo, todo }) => {
  return (
    <div className='todo-item'>
      <div className="todo_account">{todo.id}</div>
      {todo.completed
        ? <div className="todo_mark" onClick={() => toggleTodo(todo.id)}><img src={mark} alt="" /></div>
        : <div className="empty_block" onClick={() => toggleTodo(todo.id)}></div>
      }
      <div className={todo.completed ? 'todo-text active' : 'todo-text'}>{todo.text}</div>

      <div className="todo_trash" onClick={() => removeTask(todo.id)}><img src={trash} alt="" /></div>

    </div>
  )
}

export default ToDoList